.About {
  width: 100%;
  overflow: hidden;
}
.about__container {
  max-width: 1200px;
  margin: 0 auto;
  height: 100vh;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.about__details {
  padding: 2rem 10rem;
  color: white;
  height: 800px;
  width: 100%;
  backdrop-filter: blur(2px) saturate(200%);
  -webkit-backdrop-filter: blur(2px) saturate(200%);
  border-radius: 30px;
  background-color: rgba(0, 0, 0, 0.23);
  z-index: 100;
}

.ny {
  left: -42%;
  top: 5%;
  position: absolute;
}

.data {
  right: -13%;
  top: 9%;
  position: absolute;
}

.top {
  display: flex;
  align-items: center;
}

h2 {
  font-size: 3.2rem;
  font-weight: 300;
  line-height: 0;
}
h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

.heading {
  margin-left: 6rem;
}

.row {
  display: flex;
}

.col-1 {
  width: 25%;
}
.col-2 {
  width: 25%;
}
.col-3 {
  font-size: 2rem;
  line-height: 1.3;
  padding-left: 2rem;
}

.languages span,
.framework span,
.skills span {
  font-size: 2.4rem;
  font-weight: bold;
}

.languages ul,
.framework ul,
.skills ul {
  margin: 2rem 0 4rem;
  text-align: left;
  padding: 0;
  list-style-type: none;
}

.languages ul li,
.framework ul li {
  display: flex;
  align-items: center;
  margin: 0.2rem;
}

.languages .icon,
.framework .icon {
  font-size: 5rem;
  margin-right: 1rem;
}

.languages p,
.framework p {
  font-size: 2rem;
  margin: 0;
  width: auto;
}

.skills ul li p {
  padding: 0 6rem 0 0;
  margin: 1rem 0;
  width: 300px;
  font-size: 1.8rem;
}

.resume {
  font-size: 2rem;
  font-weight: 700;
  text-decoration: none;
  color: white;
  position: relative;
}
.resume::before {
  position: absolute;
  content: '';
  height: 2px;
  width: 100%;
  bottom: -1px;
  background: white;
}

@media (max-width: 1124px) {
  .About {
    width: 100%;
    overflow: auto;
  }
  .top {
    align-items: center;
    justify-content: center;
    margin-bottom: 4rem;
  }

  .me {
    height: 200px;
  }

  h2 {
    font-size: 2.8rem;
    font-weight: 300;
    margin-top: 4rem;
    line-height: 0;
  }
  h3 {
    font-size: 2rem;
    font-weight: 300;
  }

  .heading {
    margin: 0 2rem 0;
  }

  .row {
    flex-direction: column-reverse;
  }

  .col-1 {
    display: flex;
    flex-wrap: wrap;
    width: 80%;
    margin: 0 auto;
  }
  .col-2 {
    display: flex;
    flex-wrap: wrap;
    width: 80%;
    margin: 0 auto;
  }

  .col-3 {
    width: 80%;
    margin: 0 auto 4rem;
    padding: 0;
  }

  .skills ul li {
    margin: 1rem 0;
    width: 22%;
    padding: 1rem;
  }

  .skills ul li:last-child {
    margin: 1rem 0;
    width: 300px;
    padding: 1rem;
  }

  .languages ul li,
  .framework ul li {
    margin-right: 2rem;
    margin-bottom: 2rem;
  }

  .languages .icon,
  .framework .icon {
    font-size: 3rem;
    margin-right: 1rem;
  }

  .languages ul,
  .framework ul,
  .skills ul {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-wrap: wrap;
  }

  .resume__container {
    margin-left: auto;
    margin-bottom: 4rem;
  }
  .about__details {
    position: absolute;
    top: 0;
    padding-left: 3rem;
    padding-right: 3rem;
    padding-top: 8rem;
    border-radius: 0;
    height: max-content;
    background-color: rgba(0, 0, 0, 0.5);
  }
}

@media (max-width: 729px) {
  .skills ul li {
    width: 100%;
    padding: 0;
  }

  .skills ul li:last-child {
    width: 100%;
    padding: 0;
  }

  .top {
    flex-direction: column;
    justify-content: center;
    text-align: center;
  }
  .ny {
    left: -80%;
  }
}

@media (max-width: 996px) {
  .col-3 {
    font-size: 1.6rem;
  }
  .skills ul li p {
    font-size: 1.6rem;
    width: 100%;
    padding: 0;
  }
}
